import React from 'react';
import type { ZhiFactory } from '@ali/4ever-cangjie';
import { TemplateButton } from '../mo';
import { moFactory } from '../mo/index';

export const zhiFactory: ZhiFactory = () => {
  return {
    ...moFactory({}),
    renderNode(props, _, next) {
      if (!TemplateButton.isTemplateButton(props.node)) return next();
      return <></>;
    },
    models: {
      klass: 'block',
      type: TemplateButton.TYPE,
    },
  };
};
